.user-item {

    margin: 1rem;
  
    width: calc(20% - 2rem);
  
    min-width: 17.5rem;
  
  }
  .itemimage
  {
    width: 100%;

  height: 100%;

  display: flex; 

  justify-content: center;

  align-items: center;

  
  }

  .itemimage img{
    display: block;

    border-radius: 50%;
    
  width: 100%;
  
    height: 100%;
  
    object-fit: cover;
     
  }

  .card 
  {
    margin: 0;
 
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
   
     border-radius: 25px;
   
     padding: 0rem;
    
    overflow: hidden;
   

     background: white;
  }
  
  
  .user-item a 
  {
  
    display: flex;
  
    align-items: center;
  
    width: 100%;
  
    height: 100%;
  
    text-decoration: none;
  
    padding: 1rem;
  
    color: white;
  
    background: #292929;
  
  }
  
  
  .user-item a:hover,
  
  
  .user-item a:active
   {
  
    background: #ffd900;
  
  }
  

  
  .user-item__content 
  {
  
    padding: 0;
  
  }
  
  
  .imageitem
   {
  
    width: 4rem;
  
    height: 4rem;
  
    margin-right: 1rem;
  
  }
  
  
  
  .user-item__info h2
   {
    
  font-size: 1.5rem;
  
  margin: 0 0 0.5rem 0;
  
    font-weight: normal;
  
    color: #ffd900;
  
  }
  
  
  
  .user-item:hover h2,
  
  
  .user-item:active h2,
  
  
  .user-item:hover h3,
  
  
  .user-item:active h3 
  {
    color: #292929;
  
  }
  
  
  .user-item__info h3
   {
  
    margin: 0;
  
  }