.nav-links {
 
    list-style: none;
   
     margin: 0;
   
     padding: 0;
    
    width: 100%;
   
     height: 100%;
   
     display: flex;
   
     flex-direction: column;
   
     justify-content: left;
   
     align-items: center;
   
   }
   
   
   .nav-links li {
   
     margin: 1rem;
   
   }
   
   
   .nav-links a {
   
     border: 2px solid transparent;
   
     color: #292929;
    
    text-decoration: none;
   
     padding: 0.5rem;
   
   border-radius: 15px;
    }

   
   
   .nav-links a:hover,
   
   .nav-links a:active,
   
   .nav-links a.active {
   
     background: #f8df00;
   
     border-color: #292929;
   
     color: #292929;
   }
   
   
   .nav-links button {
   
     cursor: pointer;
   
     border: 2px solid #292929;
   
     color: #292929;
   
     background: transparent;
   
     padding: 0.5rem;
   
     font:large;
   
   }
   
   
   .nav-links button:focus {
   
     outline: none;
   
   }
   
   
   .nav-links button:hover,
   
   .nav-links button:active 
   {
   
     background: #292929;
   
     color: white;
   
   }
   
   
   @media (min-width: 768px) {
   
     .nav-links {
   
       flex-direction: row;
   
     }
   
     
   .nav-links li {
   
       margin: 0 1.5rem;
   font-size: 1.2rem;
     }
   
     
   .nav-links a {
       color: white;
    
      text-decoration: none;
   
     }
   
     
   .nav-links button {
   
       border: 1px solid white;
   
       color: white;
   
       background: transparent;
     
   }
     
     
   .nav-links button:hover,
     
   .nav-links button:active {
   
       
   background: #f8df00;
   
       color: #292929;
   
     }
   }
   